import type { ReactNode } from 'react';
import { createContext, useContext, useEffect, useState } from 'react';

import { authSessionStorage } from '@/utils/storage';

type AuthInfo = {
  email: string;
  name: string;
  token: string;
};

type AuthContextData = {
  authInfo: AuthInfo | undefined;
  setAuthInfo: (authInfo: AuthInfo) => void;
  login: (token: string, email: string, name: string) => void; //  
  logout: () => void; //  
};

export const AuthContext = createContext<AuthContextData | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const currentAuthToken = authSessionStorage.get();
  const [isReady, setIsReady] = useState(false); //  false 

  const [authInfo, setAuthInfo] = useState<AuthInfo | undefined>(undefined);

  const handleAuthInfo = (currentAuthInfo: AuthInfo) => {
    setAuthInfo(currentAuthInfo);
    authSessionStorage.set(currentAuthInfo.token);
  };

  const login = (token: string, email: string, name: string) => {
    handleAuthInfo({ email, name, token });
  };

  const logout = () => {
    authSessionStorage.set(undefined);
    setAuthInfo(undefined); //  
  };

  useEffect(() => {
    if (currentAuthToken) {
      //  API       
      const fetchUserInfo = async () => {
        try {
          //       
          const userInfo = {
            email: currentAuthToken,
            name: currentAuthToken,
            token: currentAuthToken,
          };
          setAuthInfo(userInfo);
        } catch (error) {
          console.error('   .', error);
        }
      };
      fetchUserInfo();
    }
    setIsReady(true); //    
  }, [currentAuthToken]);

  if (!isReady) return <></>;
  return (
    <AuthContext.Provider value={{ authInfo, setAuthInfo: handleAuthInfo, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
