import styled from '@emotion/styled';
import { useState } from 'react';
import { Link, useSearchParams } from 'react-router-dom';

import { fetchWithTokenInstance } from '@/api/instance';
import KAKAO_LOGO from '@/assets/kakao_logo.svg';
import { Button } from '@/components/common/Button';
import { UnderlineTextField } from '@/components/common/Form/Input/UnderlineTextField';
import { Spacing } from '@/components/common/layouts/Spacing';
import { RouterPath } from '@/routes/path';
import { breakpoints } from '@/styles/variants';
import { authSessionStorage } from '@/utils/storage';

export const JoinPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [queryParams] = useSearchParams();

  const handleConfirm = async () => {
    if (!email || !password) {
      alert('  .');
      return;
    }
    console.log(' :', { email, password });

    try {
        //TODO: API 
        const response = await fetchWithTokenInstance().post('api/members/register', {
            email, password
        })
        console.log(' :', response.data);
        const { token } = response.data
        authSessionStorage.set(token)
        
        const redirectUrl = queryParams.get('redirect') ?? `${window.location.origin}/`;
        console.log(' URL:', redirectUrl);
        return window.location.replace(redirectUrl);
    } catch (error) {
        console.error(' ', error)
        alert(' .')
    }

    //authSessionStorage.set(id);   //API     


  };

  return (
    <Wrapper>
      <Logo src={KAKAO_LOGO} alt=" CI" />
      <FormWrapper>
        <UnderlineTextField placeholder="" value={email} onChange={(e) => setEmail(e.target.value)} />
        <Spacing />
        <UnderlineTextField
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />

        <Spacing
          height={{
            initial: 40,
            sm: 60,
          }}
        />
        <Button onClick={handleConfirm}></Button>
        <Other><Link to={RouterPath.login}></Link></Other>
      </FormWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const Logo = styled.img`
  width: 88px;
  color: #333;
`;

const FormWrapper = styled.article`
  width: 100%;
  max-width: 580px;
  padding: 16px;

  @media screen and (min-width: ${breakpoints.sm}) {
    border: 1px solid rgba(0, 0, 0, 0.12);
    padding: 60px 52px;
  }
`;

const Other = styled.div`
  display: flex;
  margin-top: 30px;
  justify-content: center;
  align-items: center;
  color: #4C4C4C;
  text-decoration: underline;
  cursor: pointer;
`